$(function(){
    $('.list1>li').bind('mouseover',function(){
        $(this).find('.list2').show();
    })
    $('.list1>li').bind('mouseout',function(){
        $(this).find('.list2').hide();
    })
    $('.list1>li').click(function(){
        $(this).siblings().removeClass('active');
        $(this).addClass('active');
    })
    var index=0;
    var abtn;
    var len=$('.slider>ul>li').length;
    var  timer;
     timer=setInterval(function(){
        next();
    },3000)
   $('.slider').bind('mouseover',function(){
       clearInterval(timer);
       $('#prev').show();
       $('#next').show();
   })
   $('.slider').bind('mouseout',function(){
       clearInterval(timer);
       $('#prev').hide();
       $('#next').hide();
       timer=setInterval(function(){
           next();
       },3000)
   })
    $('#prev').click(function(){
        clearInterval(timer);
        prev();
    })
    $('#next').click(function(){
        clearInterval(timer);
       next();
    })
    function prev(){
        index--;
        if(index<0){
            index=len-1;
        }
        setSlider(index);
    }
    function next(){
        if(!abtn){
            abtn=true;
            setSlider(index);
        }else{
            index++;
            if(index>len-1){
                index=0;
            }
            setSlider(index);
        }
    }
    function setSlider(index){
        $('.slider>ul>li').eq(index).addClass('cur').siblings().removeClass('cur');;
        $('.dots li').eq(index).addClass('cur').siblings().removeClass('cur')

    }
    function setDots(index){

    }

})


